.list-anime{
  display: flex;
  flex-wrap: wrap;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
  justify-content: center;
  width: 100%;
  margin: auto;
}

.container-genre-detail .list-anime{
  margin-top: 1rem;
}

.list-anime-nowrap{
  display: table;
  width: fit-content;
  white-space: nowrap;
  position: relative;
  overflow-y: hidden;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
  box-shadow: 0px 0px 15px 2px rgb(51, 57, 92);
  margin-top: 0.4rem;
  touch-action: pan-y;
}

.block-list-anime{
  width: 100%;
  height: 100%;
  content: "";
  position: absolute;
  display: block;
  z-index: 2;
}

.empty{
    width: 100%;
    height: 100%;
    text-align: center;
    height: 300px;
}

.list-anime-nowrap .anime-item-info{
  white-space: normal;
  text-align: center;
}

.list-anime-nowrap::-webkit-scrollbar{
  display: none;
}

.list-anime-nowrap::-webkit-scrollbar-thumb{
  display: none;
}

.list-anime-nowrap::-webkit-scrollbar-thumb:hover{
  background-color: white;
}



.list-anime-nowrap .anime-item{
  display: inline-block;
  max-width: 250px;
}